// 
import langs from '../../lang'

// 
export default {
  props: {
    // 
    // -  'zh-CN' ()  'en'
    // - 
    i18n: {
      type: [ String, Object ],
      default: 'zh-CN'
    }
  },

  computed: {
    // 
    lang () {
      let lang = null
      let i18n = this.i18n

      if (typeof i18n === 'string') {
        lang = langs[i18n]
      } else if (typeof i18n === 'object') {
        lang = i18n
      }

      // 
      if (!lang) lang = langs['en']

      return lang
    }
  }
}
